.login-form-container {
    width: 100%;
    padding: 2% 3%;
    margin: 0;
    display: block;
    justify-content: center;
    position: relative;
    top: 50%;
    background-color: rgb(19, 17, 18);
    background-color: linear-gradient(top top, #121212 65%);
    height: 65%;
    border-radius: 10px;
  }

  .logo {
    margin-top: 10px;
    width: 100px;
    height: auto;
    position: relative;
    left: 35%;
    cursor: pointer;
  }
  
  hr {
    margin-top: 9px;
    background-color: rgb(0, 0, 0);
    border-radius: 20px;
  }